import { NgModule }               from '@angular/core';
import { ErrorHandler }           from '@angular/core';

import { HttpModule }             from '@angular/http';
import { Storage }                from '@ionic/storage';
import { IonicApp, IonicModule }  from 'ionic-angular';

import { SQLite }                 from 'ionic-native';

import { Geolocation }              from 'ionic-native';

import { FuelITApp }              from './app.component';

import { LoginPage } from '../pages/login/login';
import { SettingsPage } from '../pages/settings/settings';
import { TabsPage } from '../pages/tabs/tabs';
import { CreateTimesheetPage } from '../pages/timesheet/timesheet';
import { CreateTransactionPage } from '../pages/transaction/transaction';

import { CapitalizePipe } from '../pipes';
import { Config } from '../config';

import { ConfigurationService } from '../providers/configuration-service';
import { OperatorService }      from '../providers/operator-service';
import { PlantService }         from '../providers/plant-service';
import { TimesheetService }     from '../providers/timesheet-service';
import { TransactionService }   from '../providers/transaction-service';
import { SqlService }           from '../providers/sql-service';
import { BluetoothService }     from '../providers/bluetooth-service';
import { RollbarErrorHandler }  from '../providers/rollbar-service';

@NgModule({
  declarations: [
    FuelITApp,
    LoginPage,
    SettingsPage,
    TabsPage,
    CreateTimesheetPage,
    CreateTransactionPage,
    CapitalizePipe
  ],
  imports: [
    IonicModule.forRoot(FuelITApp, {scrollAssist: false}),
    HttpModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    FuelITApp,
    LoginPage,
    SettingsPage,
    TabsPage,
    CreateTimesheetPage,
    CreateTransactionPage
  ],
  providers: [
    ConfigurationService,
    OperatorService,
    PlantService,
    TimesheetService,
    TransactionService,
    SqlService,
    Config,
    Storage,
    SQLite,
    Geolocation,
    BluetoothService,
    { provide: ErrorHandler, useClass: RollbarErrorHandler }
  ]
})
export class AppModule { }



